<template>
    <div class="content">
        <div class="search">
            <input @input="bindKeyInput" type="text" placeholder="搜索歌曲" class="inputSearch">
            <i @click="searchI" style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
        </div>
        <div style="margin-top:20rpx;">
            <div v-for="item in musicData" :key="item[0].id" class="SingleWidth" @click="musicUrl(item[0].id)">
                <div>
                    <img :src="item[0].al.picUrl" alt="">
                    <div>
                        <p class="name">{{item[0].name}}</p>
                        <div class="people">
                            <p v-for="(itempeo,indexa) in item[0].ar" :key="indexa">
                                {{itempeo.name}} &nbsp;
                            </p>
                        </div>
                    </div>

                </div>
            </div>
        </div>
        <div class="Playing" @click="listenerButtonPause">
            <div class='wrap'>
                <i style="background:url(data:image/svg+xml;base64,); background-size:80rpx 80rpx; width:80rpx;height:80rpx;margin-top:20rpx;margin-left:20rpx"></i>
            </div>

        </div>
        <div class="Refresh" @click="Refresh">
            <div class='wrap'>
                <i style="background:url(data:image/svg+xml;base64,); background-size:80rpx 80rpx; width:80rpx;height:80rpx;margin-top:20rpx;margin-left:20rpx"></i>
            </div>

        </div>
    </div>
</template>

<script>
var Fly = require("flyio/dist/npm/wx"); //npm引入方式
var fly = new Fly(); //创建fly实例
export default {
    data() {
        return {
            songItem: {},
            musicData: [],
            playing: []
        };
    },
    onLoad: function() {
        let playing = wx.getStorageSync("playing");

        let playingzheng = [];
        wx.setNavigationBarTitle({
            title: "播放列表" //页面标题为路由参数
        });

        playing = playing.reverse();
        for (let index = 0; index < playing.length; index++) {
            const element = playing[index];

            var musicDetails =
                "http://localhost:3000/song/detail?ids=" + element.id;
            fly
                .get(musicDetails, {})
                .then(d => {
                    this.musicData.push(d.data.songs);
                })
                .catch(err => {
                    console.log(err.status, err.message);
                });
        }
    },
    methods: {
        bindKeyInput: function(e) {
            this.bindKeyInput = e.target.value;
        },
        searchI: function() {
            let searchValue = this.bindKeyInput;

            const url = "../search/main?search=" + searchValue;
            wx.navigateTo({ url });
        },
        musicUrl(e) {
            const url = "../music/main?id=" + e;
            wx.navigateTo({ url });
        },
        //监听button暂停按钮
        listenerButtonPause: function() {
            let e = wx.getStorageSync("musicId");

            if (!e) {
            } else {
                const url = "../music/main?id=" + e;
                wx.navigateTo({ url });
            }
        },
        Refresh: function() {
            let playing = wx.getStorageSync("playing");

            playing = playing.reverse();
            for (let index = 0; index < playing.length; index++) {
                const element = playing[index];

                var musicDetails =
                    "http://localhost:3000/song/detail?ids=" + element.id;
                fly
                    .get(musicDetails, {})
                    .then(d => {
                        this.musicData.push(d.data.songs);
                    })
                    .catch(err => {
                        console.log(err.status, err.message);
                    });
            }
        }
    }
};
</script>

<style scoped>
.content {
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 0 40rpx;
}
.search {
    display: flex;
}
.inputSearch {
    flex: 1;
    margin-right: 20rpx;
    border-radius: 60rpx;
    border: 1px solid #7054dd;
    font-size: 24rpx;
    padding-left: 20rpx;
}

.Playing {
    position: fixed;
    bottom: 20rpx;
    right: 40rpx;
    width: 120rpx;
    height: 120rpx;
    background: #fff;
    border: 2px solid #7054dd;
    border-radius: 50%;
    overflow: hidden;
}
.Refresh {
    position: fixed;
    bottom: 20rpx;
    left: 40rpx;
    width: 120rpx;
    height: 120rpx;
    background: #fff;
    border: 2px solid #7054dd;
    border-radius: 50%;
    overflow: hidden;
}

.SingleWidth {
    width: 670rpx;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.SingleWidth > div {
    display: flex;
    margin-bottom: 30rpx;
}
.SingleWidth > div img {
    width: 120rpx;
    height: 120rpx;
    border-radius: 10rpx;
}
.SingleWidth > div > div {
    margin-left: 20rpx;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.name {
    font-size: 32rpx;
}
.people {
    margin-top: 10rpx;
    font-size: 24rpx;
    display: flex;
    color: #999;
    max-width: 420rpx;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
</style>



